import { Component, OnInit } from '@angular/core';
import { Movie } from './../../../movieModel/movieModel';
import { MovieService } from './../../movie.service';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { ActivatedRoute, Router, Params, ParamMap } from '@angular/router';
import { Observable } from 'rxjs';
import { Location } from '@angular/common';
@Component({
  selector: 'app-allmovie',
  templateUrl: './allmovie.component.html',
  styleUrls: ['./allmovie.component.scss']
})
export class AllmovieComponent implements OnInit {
  type:String;
  movieType:Movie[];
  movies: any[];

  constructor(
    private movieService: MovieService,
    private route:ActivatedRoute,
    private location:Location
  ){}

  private allItems: any[];
  pager: any = {};
  pagedItems: any[];


  ngOnInit() {
    this.getType();
    this.getSearchMovie();
    this.getTypeYear();
  }

  getType(): void{
    this.route.paramMap.subscribe((params: ParamMap) => {
      const type = params.get('type');
      this.type=type;
      this.movieService.getMovieFromType(type).subscribe(
        (movie) => {
          // console.log(movie);
          // this.movieType = movie;
          this.movies=movie;
          this.setPage(1);
        }
      );
    })
  }

  getSearchMovie(): void{
    this.route.paramMap.subscribe((params: ParamMap) => {
      const type = params.get('type');
      this.type=type;
      console.log(this.type);
      this.movieService.getMovieFromSearch(type).subscribe(
        (movie) => {
          this.movies=movie;
          this.setPage(1);
        }
      );
    })
  }

  getTypeYear(): void{
    this.route.paramMap.subscribe((params: ParamMap) => {
      const type = params.get('type');
      this.type=type;
      this.movieService.getMovieFromTypeYear(type).subscribe(
        (movie) => {
           console.log("movie");
          // this.movieType = movie;
          this.movies=movie;
          this.setPage(1);
        }
      );
    })
  }

  goBack():void {
      this.location.back();
  }

  setPage(page: number) {
    // get pager object from service
    this.pager = this.movieService.getPager(this.movies.length, page);

    // get current page of items
    this.pagedItems = this.movies.slice(this.pager.startIndex, this.pager.endIndex + 1);
}


}
