import { Component, OnInit } from '@angular/core';
import { MovieService } from './../../movie.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-watchmovie',
  templateUrl: './watchmovie.component.html',
  styleUrls: ['./watchmovie.component.scss']
})
export class WatchmovieComponent implements OnInit {
  movie: any;
  movies:any;
  constructor(
    private route:ActivatedRoute ,
    private movieService:MovieService
  ) {}

  ngOnInit() {
    this.movieDetail();
  }

  movieDetail(): void{
    const id = +this.route.snapshot.paramMap.get('id');
    this.movieService.getMovieFromId(id).subscribe((movie) => {this.movie = movie;//console.log(this.movie[0].genre)
      const category= this.movie[0].release_year;
      console.log(category);
      this.movieService.getMovieFromCategory(category).subscribe(
        (category)=>{
          this.movies = category;
        }
      );
    });
    // console.log(this.movie);//hàm sẽ chạy dòng 28 trước 27 vì bất đồng bộ
    // this.movieService.getMovieFromCategory(id).subscribe((movies) => {this.movies = movies});
    // console.log(this.movie);
    // console.log(id);
  }


}
