import { Component, OnInit, Input, NgZone } from '@angular/core';
import { Movie } from '../../../movieModel/movieModel';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { MovieService } from './../../movie.service';

@Component({
  selector: 'app-detailmovie',
  templateUrl: './detailmovie.component.html',
  styleUrls: ['./detailmovie.component.scss']
})
export class DetailmovieComponent implements OnInit {
  movie: any;
  movies: Movie[];
  constructor(
    private route:ActivatedRoute ,
    private movieService:MovieService ,
    private location:Location ,
  ) {}

  ngOnInit() {
    this.movieDetail();
  }

  movieDetail(): void{
    const id = +this.route.snapshot.paramMap.get('id');
    // console.log("11");
    this.movieService.getMovieFromId(id).subscribe((movie) => {this.movie = movie;console.log(this.movie);
      const category= this.movie[0].release_year;
      // console.log(id);
      this.movieService.getMovieFromCategory(category).subscribe(
        (category)=>{
          this.movies = category;
        }
      );
    });
    // console.log(this.movie);//hàm sẽ chạy dòng 28 trước 27 vì bất đồng bộ
    // this.movieService.getMovieFromCategory(id).subscribe((movies) => {this.movies = movies});
    // console.log(this.movie);
    // console.log(id);
  }


}
