import { Component, OnInit } from '@angular/core';
import { MovieService } from './../../movie.service';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  moviebo:any;
  moviele:any;
  constructor(
    private movieService: MovieService
  ) { }

  ngOnInit() {
    this.get8Moviebo();
    this.get8Moviele();
  }

  get8Moviebo(): void{
    this.movieService.get8Moviebo().subscribe(
      ( Movie ) => {
        this.moviebo = Movie;
      }
    )
  }

  get8Moviele(): void{
    this.movieService.get8Moviele().subscribe(
      ( Movie ) => {
        this.moviele = Movie;
      }
    )
  }

}
